article, section {
  &:focus {
    outline: none !important; } }

#page {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: 0 auto;
  transition: opacity .5s ease;
  &.page-hidden {
    opacity: 0; } }

#masthead .top, #content, .footer-wrap {
  max-width: $viewport;
  margin: 0 auto; }

#masthead, #content {
  width: 100%; }

#content {
  margin-top: 80px;
  min-height: 90vh; }

section {
  .entry-footer {
    text-align: center; } }

figure.header {
  height: 600px;
  position: relative; }
figure.noheader {
  margin-bottom: -40px; }

span.author {
  text-transform: capitalize; }

.userinfo {
  display: flex;
  padding: 100px 0;
  flex-flow: row wrap;
  > * {
    padding: 20px;
    flex: 0 1 600px; }
  img {
    border-radius: 50%;
    height: 300px;
    flex: 0 0 300px;
    background-size: cover; } }

//
//buttons
//
.content-button,
.more-link,
.wpcf7-form input,
input[type="submit"] {
  transition: color .3s ease, background .3s ease; }

.content-button,
.more-link {
  background: $white;
  border: 1px solid black;
  color: $black;
  font-size: 1.5rem;
  padding: 1em 2em; }
.content-button {
  font-family: $font__title;
  font-weight: 100;
  display: inline-block;
  cursor: pointer;
  font-size: 1rem; }


.more-link, input, textarea {
  font-family: $font__title;
  line-height: 3;
  font-size: 1rem;
  padding: 0 2rem; }


input[type="submit"] {
  font-size: 1.5em; }

.more-link {
  display: inline-block;
  text-decoration: none;
  font-family: $font__title;
  margin-bottom: $gap; }

figure .more-link {
  background: $yellow;
  border: 0 none;
  margin-bottom: 0; }

.search-form {
  input[type="search"] {
    border-color: $grey; }
  input[type="submit"] {
    margin-top: 20px;
    border-radius: 0;
    background: $yellow;
    border: 0;
    box-shadow: none;
    text-shadow: none; } }

input[type="submit"],
.content-button,
.more-link {
  &:hover {
    background: $black;
    color: $white;
    box-shadow: none; } }



body {
  &.blog, &.archive, &.search {
    .entry-header {
      margin: 0 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media(max-width: $breakpoint3) {
        flex-flow: column;
        align-items: stretch; }
      .overview {
        flex: 1 1 auto; } } }

  .breadcrumb {
    margin-bottom: 0.3em;
    .current-page, a {
      color: white; } }

  .category_structure {
    display: flex;
    flex-flow: row wrap;
    font-size: 1.3em;
    >li:not(:last-child) {
      margin-right: 10px; }
    .current-cat a {
      background-color: black;
      color: white; }
    a {
      font-family: $font__title;
      padding: 0.3em;
      background: darken(white, 5);
      display: inline-block;
      margin-bottom: 0.3em;
      &:hover {
        background-color: black;
        color: white; } } } }
.wrap {
  padding: $gap; }
.columns {
  padding: 0;
  .column {
    padding: 0 15px;
    &:first-child {
      padding-left: 0; }
    &:last-child {
      padding-right: 0; } } }

.single .wrap aside,
.grey {
  margin: 20px 0;
  padding: 0 20px;
  ul {
    margin: 20px 0px; } }

.single {
  .wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-flow: row wrap;
    aside {
      background: $grey;
      flex: 0 0 33%;
      h3 {
        .entry-title {
          display: inline-block;
          text-overflow: ellipsis;
          max-width: 200px;
          white-space: nowrap;
          overflow: hidden; } } }
    .content-wrapper {
      flex: 0 0 50%; } }

  .entry-content {
    a {
      text-decoration: underline;
      &:hover {
        background: $yellow; } }
    border-bottom: 1px solid $grey; }
  .entry-meta {
    font-family: $font__title;
    border-bottom: 1px solid $grey;
    border-top: 1px solid $grey;
    padding: .6rem 0;
    .posted-on, .author {
      a {
        text-decoration: none;
        font-weight: bold; } } }
  .entry-footer {
    font-family: TradeGothicLT-BoldCondTwenty, HelveticaNeue-CondensedBold, 'Franklin Gothic Medium', sans-serif;
    span {
      display: block; }
    ul {
      display: inline-flex; }
    a {
      display: block;
      margin: 0;
      font-weight: 400;
      text-transform: uppercase;
      -webkit-font-feature-settings: 'kern';
      font-feature-settings: 'kern';
      -webkit-font-kerning: normal;
      font-kerning: normal;
      line-height: 1.1;
      white-space: nowrap;
      margin-right: 1.71429rem;
      margin-bottom: .85714rem;
      background-color: $lightgrey;
      letter-spacing: 0;
      padding: .71429rem;
      text-decoration: none;
      -webkit-transition: background-color .25s;
      transition: background-color .25s;
      &:hover {
        background-color: $grey; } } } }

.infinite-loader {
  width: 100%;
  display: block;
  height: auto;
  margin-bottom: 30px;
  .spinner {
    position: relative !important;
    margin: 0 auto;
    width: 40px !important;
    height: 40px;
    text-indent: 0;
    background: url("img/donut.svg");
    background-size: 40px 40px;
    animation: spin .5s linear infinite;
    * {
      display: none; } } }

#infinite-footer {
  display: none; }

@keyframes spin {
  50% {
    opacity: 0.3;
    -webkit-filter: grayscale(1); /* Old WebKit */;
    filter: grayscale(1); }
  100% {
    opacity: 1;
    -webkit-filter: grayscale(0); /* Old WebKit */;
    filter: grayscale(0); } }

.grid, .infinite-wrap {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  padding: 0;
  .more-link {
    font-size: 1em;
    display: inline; }
  figure {
    overflow: hidden;
    background-color: $white;
    img {
      transition: transform .3s ease, opacity .3s ease; }
    &:hover {
      img {
        transform: scale(1.1); } }

    span.icons i, figcaption h1 {
      font-size: 1.5em;
      color: white; }
    span.icons {
      align-items: center;
      position: absolute;
      right: 0.5em;
      top: 0.5em;
      display: flex;
      text-shadow: 1px 2px 7px #000;
      i {
        margin: 0 .1em; } }

    figcaption {
      >div {
        background: black;
        padding: 0.5em;
        margin: 0 0 .5em 0;
        position: relative; }

      h1 {
        padding: 0;
        margin: 0;
        word-break: break-word;
        max-height: 4.5em;
        overflow: hidden;
        hyphens: auto;
        display: block; }

      h2, h3, h4, h5 {
        display: none; }
      a:empty {
        display: none; } } }
  > article {
    flex: 0 0 25%;
    padding: $gap;
    @media (max-width: $breakpoint1) {
      flex: 0 0 33.33333%;
      max-width: none; }
    img {
      width: 100%;
      height: 100%; } } }

.tommy {
  i, svg {
    animation-name: heart;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: ease;
    padding: 0 5px;
    animation-play-state: paused; } }

.entry-title, .page-title, .tommy, span.icons {
  i, svg {
    transition: color .3s ease, transform .3s ease; } }

.entry-title, .page-title, span.icons {
  &:hover {
    i,svg {
      // animation-play-state: running
      transform: scale(1.15); } } }

.entry-title, .page-title {
  .fa {
    margin: 0 0.3em;
    &:before {
      color: $white; }
    aside & {
      margin-left: 0.3em;
      line-height: 2em;
      vertical-align: text-bottom;
      &:before {
        color: $black; } } } }


nav.pagination {
  margin: 35px auto;
  a, span {
    display: inline-block;
    border: 1px solid $black;
    padding: 5px 10px;
    text-decoration: none;
    &.current, &:hover {
      background: $black;
      color: $white; } } }
