.badge {
  transition: opacity .3s ease;
  box-sizing: content-box;
  background-size: 40px;
  padding: 0 60px $gap 250px;
  text-align: left;
  width: 320px;
  top: 50%;
  left: 50%;
  margin-left: -310px;
  margin-top: -203px;
  > div {
    padding: 0;
    margin: 0;
    &:before {
      display: block;
      content: "";
      width: 270px;
      height: 270px;
      position: absolute;
      left: -50px;
      top: -50px;
      background-size: contain; } } }

section.not-found {
  position: absolute;
  &.badge {
    >div {
     &::before {
      top: 0;
      left: 0;
      width: 200px;
      height: 200px;
      background-image: url("img/donut.svg"); } } } }

#nlpopup {
  position: fixed;
  .wpcf7:before {
    background-image: url("img/donut.svg");
    left: -100px; }
  .badge {
    padding: 0 60px 30px 200px;
    background: #fff no-repeat 30px 380px;
    &::before {
      background-image: url("img/2000px-TK_email_icon.svg.png"); } } }
body.nl-open {
  overflow: hidden;
  margin-right: 20px;
  #masthead, #content, .footer-wrap {
    filter: blur(4px); } }

#nlpopup {
  &, >div {
    z-index: 999999999999999;
    opacity: 0;
    position: fixed; }

  pointer-events: none;
  transition: opacity .3s ease;
  color: $darkgrey;
  z-index: 999999999999999;


  &:after {
    content: "";
    background-color: rgba(0, 0, 0, 0.2);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 999999; }

  &.active {
    opacity: 1;
    pointer-events: all;
    > div {
      opacity: 1; } } }

#nlpopup {
  h1 {
    line-height: 36px;
    padding: 0;
    margin: 60px 0 20px;
    color: $black;
    font-size: 4rem; }
  .closex {
    display: block;
    cursor: pointer;
    width: 19px;
    height: 19px;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 20px;
    right: 20px;
    &:before {
      font-size: 2em; } } }
