.wpcf7-form-control-wrap input {
  margin-bottom: 1em;
  border-radius: 0; }

.expired {
  display: block;
  margin-top: 10px;
  background: #fffbc0;
  -moz-border-radius: 20px;
  -webkit-border-radius: 20px;
  border: 1px solid #e3d591;
  padding: 30px;
  color: #373737;
  margin-bottom: 20px;
  font-size: 18px;
  width: 299px;
  margin-left: 60px; }
